import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ValidationService } from 'common/validationService/validation.service';
import { HttpUserUtilsService } from 'core/http_wrapper/http-user-utils.service';
import { HttpSuccesFailureResponse } from 'core/http_wrapper/http_wrapper_response';
import { Constants, MixpanelEvent } from 'core/constants';
import { LocalStorageService } from 'core/service/local-storage.service';
import { ActivatedRoute, Router } from '@angular/router';
import { HttpCommonUtilsService } from 'core/http_wrapper/http-common-utils.service';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { MixpanelService } from 'core/mixpanelService.service';
import { GlobalSubscriptionService } from 'app/globalSubcription.service';
import { UserSession } from 'core/sessions';
import { ClassDetailResponseDetail, ClassDayList, ClassDayListNew,classTimingDateWiseList } from 'core/models/classDetailByIdResponse';
import { ClassDetail } from 'core/models/classListResponse';
import { Modal, overlayConfigFactory } from 'angular2-modal';
import { ConfirmpopupComponent as confirmpopupcomponent } from '../common/confirmpopup/confirmpopup.component';
import { BSModalContext } from 'angular2-modal/plugins/bootstrap';

declare var $: any;

@Component({
  selector: 'app-special-class-promotion',
  templateUrl: './special-class-promotion.component.html',
  styleUrls: ['./special-class-promotion.component.css']
})
export class SpecialClassPromotionComponent implements OnInit,HttpSuccesFailureResponse {
   public  loginForm: FormGroup;
   public formLogin:FormGroup;
   public errorInApi;
   public shareurl = Constants.WebHostUrl + 'specialProgram';
   isLoggedIn:boolean;
   userdetails;
   name="";
   email="";
   password = "";
   userId:any;
   public sub:any;
   emailNotAvailable=false;
   isVideoPackage:boolean;
   specialProgramDetail;
   specialProgramDetailArray:any[] = [{}]
   sessionDetail:ClassDetail;
   classDetail;
   userJoinStatus:boolean=false;
   public selectedKidForClass:any;
   isFromMobile=false;


invalidGender=false;
public alertMessage="After making the payment, you will be automatically booked to all the sessions.";
public upcommingClassSession=[];
headerTitleGYMRegular=[{name:"Date",view:false,id:0},{name:"Time",view:false,id:0}]
availableBurnSeat: any;
classId: any;

  constructor(private formBuilder: FormBuilder,private user_http: HttpUserUtilsService,
    private _userAuthHttpService: HttpUserAuthUtilsService,private http_common:HttpCommonUtilsService,private route:ActivatedRoute,private router:Router,private mixpanel: MixpanelService,private globalSubscriptionService: GlobalSubscriptionService,public modal: Modal) { }
  onSuccess(type: any, responsedata: any) {
    switch(type){
     case Constants.FindUserApi:
      if(this.isFromMobile){
        this.setLoginDataForMobile(responsedata)

      }else{
        LocalStorageService.setUser(responsedata);

        this.userdetails=LocalStorageService.getUser();
        this.isLoggedIn = LocalStorageService.getIsLoggedIn();

        this.purchaseNow();

      }
       
       break

      case Constants.TrainerUpcomingClassesApi:
        this.upcommingClassSession = responsedata.data;
        if(this.upcommingClassSession&&this.upcommingClassSession.length>0){
          this.sessionDetail = this.upcommingClassSession[0];

          this._userAuthHttpService.getSessionDetail(this.upcommingClassSession[0].classSessionId,this,true)

        }
        
        break
        case Constants.SessionDetailIdApi:
          this.userJoinStatus= responsedata.userJoinStatus
          
          this.availableBurnSeat = responsedata.availableSeat
                  
          this.sessionDetail.autoJoinPackageDetail = responsedata.autoJoinPackageDetail;
          try{
            this.sessionDetail.autoJoinPackageDetail.gymId = this.sessionDetail.gymBranchDetail.gymId; 
            this.sessionDetail.autoJoinPackageDetail.sessionId = this.sessionDetail.classSessionId; 
            if(LocalStorageService.getIsLoggedIn()){
              this.userdetails = LocalStorageService.getUser();
              let userKidsList =this.userdetails.data.userKidsList;
               if(userKidsList&&userKidsList.length==1&&this.userdetails.ageCategoryType=="CHILD"){
                this.selectedKidForClass = userKidsList[0];
              }
            }
          }catch(e){
      
          }
         //this.openModel();
         if(this.classId)
         this._userAuthHttpService.getClassDetail(this.classId, this, true);

        break
      case Constants.ClassDetailIdApi:
        this.classDetail = responsedata;
        this.specialProgramDetailArray[0].title = this.classDetail.title;
        this.specialProgramDetailArray[0].subTitle = this.classDetail.gymBranchDetail.gymName;
      let classTimingDateWise;
       this.classDetail.classTimingDateWiseList.forEach(element => {
        if(UserSession.checkisPastDate(element.fromDate)&&UserSession.checkisPastDate(element.toDate)){
          element.isExpired = true;
        }   
       });

       this.classDetail.classTimingDateWiseList.forEach(element => {
        if(!element.isExpired){
          classTimingDateWise = element;

        }   
       });
       let openTO="";
       let programGender="Open To All Males and Females"
       if(this.classDetail.classType=='MALE'){
        openTO = "males"
        programGender = "Open To All Males";
       }
       if(this.classDetail.classType=='FEMALE'){
        openTO = "females"
        programGender = "Open To All Females";


      }
      this.specialProgramDetailArray[0].detailTitle = "Program open to all "+openTO+" from "+ UserSession.formatDate(classTimingDateWise.fromDate)+"-"+ UserSession.formatDate(classTimingDateWise.toDate); //+" ("+this.upcommingClassSession.length  +" Classes)";
      this.alertMessage=" After making the payment, you will be automatically booked to all "+UserSession.weekBetweenFromToDate(classTimingDateWise.fromDate,classTimingDateWise.toDate)*classTimingDateWise.classTimingResponseList.length+" sessions"+ " from "+ UserSession.formatDate(classTimingDateWise.fromDate)+" To "+ UserSession.formatDate(classTimingDateWise.toDate)
     

      this.specialProgramDetailArray[0].subDetail = this.classDetail.description.replace(/(?:\r\n|\r|\n)/g, '<br>');

       this.specialProgramDetailArray[0].programGender = programGender;
       let programDays="Every "
       let programTime="";

       //"1:00 PM To 1:45 PM (GMT+3)"
       classTimingDateWise.classTimingResponseList.forEach(element => {
        programDays =programDays + UserSession.dayarrayfull[element.classDay-1]+" ";  
        let programTimeLocal = UserSession.transformTime(element.fromTime)+" To " +UserSession.transformTime(element.toTime)+" (GMT+3) "+ UserSession.dayarrayfull[element.classDay-1];
        if(programTime.length==0){
          programTime = programTimeLocal;      
        }
        else{
          programTime = programTime+" <br> "+programTimeLocal
        }
      });
       this.specialProgramDetailArray[0].programDays = programDays;
       this.specialProgramDetailArray[0].programDays.trim();
       this.specialProgramDetailArray[0].programTime = programTime+"";
       this.specialProgramDetailArray[0].price=this.classDetail.price;

       this.specialProgramDetailArray[0].price=this.classDetail.price+" KWD";

       this.specialProgramDetailArray[0].packageId=this.sessionDetail.autoJoinPackageDetail.id;
       this.specialProgramDetailArray[0].image=this.classDetail.classMedia.mediaUrl;
       this.specialProgramDetailArray[0].gender=this.classDetail.classType;
       this.specialProgramDetailArray[0].packageObj=this.sessionDetail.autoJoinPackageDetail;
      break
      case Constants.specialPackageApi:
        case Constants.purchasePackageApi:
        LocalStorageService.setIsFromSpecialPlan(true);
        sessionStorage.setItem("special_class","true");
        let paymentObject = {'purchase_type':"special_class",'package_info':JSON.stringify(this.sessionDetail.autoJoinPackageDetail),'class_info':JSON.stringify(this.sessionDetail)}
        localStorage.setItem('payment_info',JSON.stringify(paymentObject));
      
       
        if(responsedata.data.paymentURL){
          window.location.assign(responsedata.data.paymentURL);

        }else{
          window.location.assign(responsedata.data.paymentUrl);

        }

       //window.location.assign(responsedata.data);
        break
        case Constants.RegisterApi:
          this.userId = responsedata.data.id;
            if(this.isLoggedIn){
              this.purchaseNow();

         } else{
     this.user_http.login(this.email,this.password,this,true);
         }  

          break
          case Constants.LoginApi:
            this.userId = responsedata.data.id;
            LocalStorageService.setStoreId(responsedata.data.storeId+"");
        this.globalSubscriptionService.onStoreUpdate(responsedata.data.storeId);        
        this.globalSubscriptionService.userLoginUpdate();
        LocalStorageService.setPassword(this.loginForm.value.password)
         if (LocalStorageService.getUserType() === 1) {
            this.mixpanel.Identify(responsedata.data.id);
            this.mixpanel.updateUserInfoToMixpanel(responsedata.data);
            this.mixpanel.logEventWithAnalytics(MixpanelEvent.loginWith, { 'origin': 'email' , 'Source' : 'web' ,
            'gender' : responsedata.data.gender})
         }
        this.globalSubscriptionService.correspondingClass('usertype');
        this.globalSubscriptionService.userLogoutUpdate();
        this.userdetails=LocalStorageService.getUser();
        this._userAuthHttpService.getUserProfile(this.userId,this,true);

        break

          case Constants.ValidateApi:
          this.emailNotAvailable = true;
         
            break   
    }
  }
  onFailure(type: any, response: any) {
    switch(type){
      case Constants.specialPackageApi:
        case Constants.RegisterApi:
       this.errorInApi =response; 
        setTimeout(() => {
          this.errorInApi = undefined; 
        }, 2000);
      break
      case Constants.ValidateApi:
             // this.user_http.purchaseSpecialPackage(this.formLogin.value.email,this.formLogin.value.name,this.userId,this.specialProgramDetail.packageId,this.isVideoPackage,this,true)
           this._userAuthHttpService.purchasePackage(this.userId, this.specialProgramDetail.packageObj,undefined, this);

        break
        case Constants.LoginApi:
          this.errorInApi =response; 
          setTimeout(() => {
            this.errorInApi = undefined; 
          }, 2000);
          break



    }
  }
  purchaseNow(){
    $("#myModal").modal("hide");
    $("#myModalold").modal("hide");

    if(this.sessionDetail.ageCategoryType&&this.sessionDetail.ageCategoryType=='CHILD'){
       
      let userKidsList =this.userdetails.data.userKidsList;
      if(!userKidsList){
       // this.router.navigate(['editprofile']);
        console.log("Add your child info");
        return this.openErrorPopup("Add your child info in your profile,", "Add Child", 'NOKIDS',"Edit Profile")
      }else{
        if(!this.selectedKidForClass){
          this.openKidsPopup("","","KIDSSP");

          console.log("Select an child to book");
          return
      
        }
        if (this.sessionDetail.classType !== 'MIXED' && this.sessionDetail.classType !== this.selectedKidForClass.gender) {
          const message = 'This is a ' + this.sessionDetail.classType.toLowerCase() + 's' + ' only class';
          const title = 'Error in joining';
          this.selectedKidForClass = undefined;

          return this.openErrorPopup(message, title, '',"Ok")
        } 
      }
      
      //this.sessionDetail.autoJoinPackageDetail.packageCategoryType = "CHILD";
      this.sessionDetail.autoJoinPackageDetail.userKidId=this.selectedKidForClass.id;

      }
    this._userAuthHttpService.purchasePackage(this.userId, this.sessionDetail.autoJoinPackageDetail,undefined, this);
   
  }
  openErrorPopup(message: string, heading: string,errorCode: string,okMsg?:string) {
    const parent = this;
    const okMessage  = okMsg?okMsg:"Ok";
    return this.modal.open(confirmpopupcomponent, overlayConfigFactory({ message, heading, parent, errorCode,okMessage}, BSModalContext));
  }
  openKidsPopup(message: string, heading: string, errorCode: string) {
    const parent = this;
    const okMessage  = "Select Kid"
    return this.modal.open(confirmpopupcomponent, overlayConfigFactory({ message, heading, parent, errorCode,okMessage}, BSModalContext));
  }
  ngOnInit() {
    this.isLoggedIn = LocalStorageService.getIsLoggedIn();

    this.sessionDetail=JSON.parse(localStorage.getItem('class_info')); 
    


    this.specialProgramDetail =    this.specialProgramDetailArray[0];

    this.isLoggedIn = LocalStorageService.getIsLoggedIn();
    if(this.isLoggedIn){
      this.userdetails=LocalStorageService.getUser();
     this.name = this.userdetails.data.firstName;
     this.email = this.userdetails.data.email;
     this.userId = this.userdetails.data.id;
     let userKidsList =this.userdetails.data.userKidsList;
      if(userKidsList&&userKidsList.length==1&&this.sessionDetail.ageCategoryType=="CHILD"){
       this.selectedKidForClass = userKidsList[0];
     }
    }
    if(LocalStorageService.getIsLoggedIn()){
     
    }
    this.sub = this.route.queryParams.subscribe(params => {
      if (params.hasOwnProperty('email') && params['email'] != null&&params.hasOwnProperty('name') && params['name'] != null) {
        this.name = params['name'];
        this.email = params['email'];
        this.userId = params['id'];

        this.isLoggedIn=true;
       console.log("email************************ "+this.email);
        
      }
      
      if (params.hasOwnProperty('pid') ){
        this.specialProgramDetail = this.specialProgramDetailArray[params['pid']-1]
        this.shareurl = Constants.WebHostUrl + 'specialProgram?pid='+params['pid']
      }else{
        if(this.specialProgramDetailArray.length==1){
          this.specialProgramDetail = this.specialProgramDetailArray[0]
          this.shareurl = Constants.WebHostUrl + 'specialProgram'
  
        }else{
          if(!this.specialProgramDetail)
          this.openProgramDialog();

        }
      }
      if (params.hasOwnProperty('classId') ){
        this.classId = params['classId'];
        this.shareurl = Constants.WebHostUrl + 'specialProgram?classId='+params['classId']
          this.user_http.getgymUpcommingClassesPage100(LocalStorageService.getUserId(), 0, this, true,params['classId'])

        

      }

      if(params.hasOwnProperty('token')){
       let token = params['token'];
       let userId = params['userId'];
       this.loginformobile(token,userId);




      }

    });


    this.loginForm = this.formBuilder.group({
      email: ['', [Validators.required, ValidationService.emailValidator]],
      firstName: ['', Validators.required],
      lastName: ['', Validators.required],
      mobile: ['', [Validators.pattern('^[0-9]*$'),Validators.minLength(8),Validators.maxLength(16),Validators.required]],

      confirmemail: [''],
      password:['',Validators.required],
      usertype: [1],
      storeId:[1],
      gender:['',Validators.required]
    });
    this.formLogin = this.formBuilder.group({
      email: [this.email, [Validators.required, ValidationService.emailValidator]],
      name: [this.name, Validators.required]   
     });

  }
  loginformobile(token: any, userId: any) {
    this.isFromMobile=true;
   LocalStorageService.setToken(token);
 this._userAuthHttpService.getUserProfile(userId,this,true);
 localStorage.setItem(Constants.authToken, token);
          


  }

  setLoginDataForMobile(responsedata){
    LocalStorageService.setUser(responsedata);
    LocalStorageService.setUserType(responsedata.data.userCategory);
    localStorage.setItem(Constants.userDataKey, JSON.stringify(responsedata));
    localStorage.setItem(Constants.usetType, responsedata.data.userCategory);
    LocalStorageService.setIsLoggedIn(true);

    this.userId = responsedata.data.id;
    this.isLoggedIn = LocalStorageService.getIsLoggedIn();
    this.userdetails=LocalStorageService.getUser();
    this.name = this.userdetails.data.firstName;
    this.email = this.userdetails.data.email;
    this.userId = this.userdetails.data.id;
    LocalStorageService.setStoreId(responsedata.data.storeId+"");
  //  this.globalSubscriptionService.onStoreUpdate(responsedata.data.storeId);        
    LocalStorageService.setPassword(this.loginForm.value.password)
  //  this.globalSubscriptionService.correspondingClass('usertype');
  //  this.globalSubscriptionService.userLogoutUpdate();    
    this.formLogin = this.formBuilder.group({
      email: [this.email, [Validators.required, ValidationService.emailValidator]],
      name: [this.name, Validators.required]   
     });
  }

  openModel(){
    if(!this.userJoinStatus){
      $("#myModalAlert").modal()

    }
  }
 
  
  setUserGender(usergender){
    
    if(this.specialProgramDetail.gender=="MIXED"||this.specialProgramDetail.gender==usergender){
      this.loginForm.patchValue({gender:usergender});
      this.loginForm.controls['gender'].updateValueAndValidity();
    }else{
this.invalidGender = true;
setTimeout(() => {
  this.invalidGender = false;
}, 5000);
    }
  

}
selectProgram(programId){
  this.specialProgramDetail = this.specialProgramDetailArray[programId-1]
        this.shareurl = Constants.WebHostUrl + 'specialProgram?pid='+programId
        $("#myModalSelectProgram").modal("hide");
        this.router.navigate(['/specialProgram'], { queryParams: { pid: programId } });


}
openProgramDialog(){
  $("#myModalAlert").modal()

}

setPaymentInfo(){

  const requestData = {'sessionId': this.sessionDetail.classSessionId, 'gymId': this.sessionDetail.gymBranchDetail.gymId}
    let paymentObject = {'purchase_type':'special_class','class_info':JSON.stringify(this.sessionDetail)}
    localStorage.setItem('payment_info',JSON.stringify(paymentObject));
}

  purchase(){
    if(this.loginForm.valid){
      this.email =this.loginForm.value.email;
      this.password = this.loginForm.value.password; 
      this.user_http.registerSp(this.loginForm.value.storeId,
        this.loginForm.value.firstName,
        this.loginForm.value.lastName,
        this.loginForm.value.password,
        this.loginForm.value.gender,
        this.loginForm.value.mobile,
        this.loginForm.value.email,0,0,this.loginForm.value.usertype,0,this,true
        )  
    }else{
  this.markFormGroupTouched(this.loginForm);
    }

    
  }

  purchaseold(){
    if(this.formLogin.valid){
      if(this.isLoggedIn){
       this.http_common.validateemail("email",this.formLogin.value.email,this,true);

      }else{
        this.user_http.login(this.formLogin.value.email,this.formLogin.value.name,this,true);

      }

   }else{
  this.markFormGroupTouched(this.formLogin);
    }

    


  }

  onModalPopupOpen(isVideoClassPass){
    this.isVideoPackage = isVideoClassPass;
  }


  private markFormGroupTouched(formGroup: FormGroup) {
    try {
        (<any>Object).values(formGroup.controls).forEach(control => {
            control.markAsTouched();
            if (control.controls) {
                control.controls.forEach(c => this.markFormGroupTouched(c));
            }
        });
    }
    catch (ex) { }
}
}



